import React, { Component,Fragment } from 'react';
import './css/AdminTable.scss';
import cookie from 'react-cookies';
import axios from 'axios';

class AdminTable extends Component {
  state = {
    tableData: null,
  }
  componentDidMount(){
    axios.post('http://ec2-52-79-234-20.ap-northeast-2.compute.amazonaws.com:5000/admin/table',{
      jwt: cookie.load('AccJWT')
    }).then(res => {
      if(res.status === 200){
        console.log(res.data)
        this.setState({tableData: res.data})
      }
    })
  }
  render() { 
    return (
      <div className="AdminTable">
        <table className="AdminTable__table">
          <tr>
            <td></td>
            <td>8 6</td>
            <td>8 7</td>
            <td>8 8</td>
            <td>8 9</td>
            <td>8 10</td>
            <td className="AdminTable__table__reason"></td>
          </tr>
          {this.renderTable()}
        </table>
        {this.props.none ? <span className="AdminTable__mean">. , X  </span>: ''}
      </div>
    );
  }
  renderTable(){
    const {tableData} = this.state;
    tableData.map(cell => {
      console.log(cell)
    })
    
  }
  renderData(array){
    return array.map(data => {
      return (
        <td>{data}</td>
      )
    })
  }
}
 
export default AdminTable;


