import React from 'react';
import Icon from './../../../assets/Icon.png';
import './css/LoginLogo.scss';

const LoginLogo = () => (
  <div className="LoginLogo">
    <img src={Icon} alt=' ' className="LoginLogo__img"/>
  </div>
)

export default LoginLogo;



