import React, { Component } from 'react';
import Clock from 'react-live-clock';
import axios from 'axios';
import cookie from 'react-cookies';
import './MainCheck.scss';

class MainCheck extends Component {
  constructor(){
    super();
    this.state = {
      code: ''
    }
  }
  componentDidMount() {
    if(!cookie.load('JWT')){
      alert('  .');
      window.location.href = '/';
    }
    this.setState({code : getParameters('code')});
  }
  render() { 
    const {code} = this.state;
    return (
      <div className="Maincontents">
        <div>
          <div className="Maincontents__days">
            <div className="Maincontents__days --s"><Clock format="DD.MM.YYYY" ticking={true}/></div>
            <div className="Maincontents__days --b"><Clock format="HH.mm.ss" ticking={true}/></div>
          </div>
          <div className="Maincontents__codeInput">
            <input style={{margin: '0 auto'}} type="text" value={code} onChange={(event)=>{this.setState({code:event.target.value})}} placeholder=""/>
            <div className="Maincontents__codeInput__btn MCodeInput" onClick={()=>this.Attendance()}>
              
            </div>
          </div>
        </div>
      </div>
    );
  }
  Attendance(){
    axios.post('http://ec2-52-79-234-20.ap-northeast-2.compute.amazonaws.com:5000/attendance/check',{
      uuid: cookie.load('JWT'),
      status: 0,
      code: this.state.code,
    }).then(res=> {
      if(res.status === 201){
        alert(' .');
      }
    })
  }
}
function getParameters(paramName) {
  //    
  var returnValue;

  //  URL 
  var url = window.location.href;

  // get      ?   slice   split  
  var parameters = (url.slice(url.indexOf('?') + 1, url.length)).split('&');

  //     paramName     return
  for (var i = 0; i < parameters.length; i++) {
      var varName = parameters[i].split('=')[0];
      if (varName.toUpperCase() == paramName.toUpperCase()) {
          returnValue = parameters[i].split('=')[1];
          return decodeURIComponent(returnValue);
      }
  }
}; 
 
export default MainCheck;


