import React, { Component } from 'react';
import './css/MCodeInput.scss';
import axios from 'axios';
import cookie from 'react-cookies';

class MCodeInput extends Component {
  constructor(){
    super();
    this.state = {
      code: ''
    }
  }
  componentDidMount() {
    if(!cookie.load('JWT')){
      alert('  .');
      window.location.href = '/';
    }
    this.setState({code : getParameters('code')});
  }
  render() { 
    return (
      <div className="MMain">
        <div className="MMain__Navi">
          <span className="MMain__Navi__ko"></span><span className="MMain__Navi__en">Attandence Check</span>
        </div>
        <div className="MMain__wrapper">
        <input className="MCodeInput__input" type="text" value={this.state.code} onChange={(event)=>{this.setState({code:event.target.value})}} placeholder=""/>
        <div className="MCodeInput__btn MCodeInput" onClick={()=>this.Attendance()}>
          
        </div>
        </div>
      </div>
    );
  }
  Attendance(){
    axios.post('http://ec2-52-79-234-20.ap-northeast-2.compute.amazonaws.com:5000/attendance/check',{
      uuid: cookie.load('JWT'),
      status: 0,
      code: this.state.code,
    }).then(res=> {
      if(res.status === 201){
        alert(' .');
      }
    })
  }
}
function getParameters(paramName) {
  //    
  var returnValue;

  //  URL 
  var url = window.location.href;

  // get      ?   slice   split  
  var parameters = (url.slice(url.indexOf('?') + 1, url.length)).split('&');

  //     paramName     return
  for (var i = 0; i < parameters.length; i++) {
      var varName = parameters[i].split('=')[0];
      if (varName.toUpperCase() == paramName.toUpperCase()) {
          returnValue = parameters[i].split('=')[1];
          return decodeURIComponent(returnValue);
      }
  }
}; 


export default MCodeInput;



